.content {
  padding: 20px 20px;

  @media (min-width: 768px) {
    padding: 20px 40px;
  }
}

.content__title {
  margin-bottom: 35px;
  font-size: 45px;

  @media (min-width: 768px) {
    margin-bottom: 50px;
    font-size: 70px;
  }
}

.content__banner {
  height: 40vh;
  max-height: 300px;
  margin-left: -20px;
  margin-right: -20px;
  margin-bottom: 35px;

  @media (min-width: 768px) {
    margin-left: -40px;
    margin-right: -40px;
    max-height: 450px;
    margin-bottom: 50px;
  }

  .content__bannerImage {
    height: 100%;
    object-fit: cover;
  }
}

.content__description {
  font-size: 18px;
  margin-bottom: 35px;

  @media (min-width: 768px) {
    font-size: 24px;
    margin-bottom: 50px;
  }
}

.content__slider {
  margin-left: -20px;
  margin-right: -20px;
  display: flex;
  gap: 20px;
  margin-bottom: 35px;

  @media (min-width: 768px) {
    margin-left: -40px;
    margin-right: -40px;
    margin-bottom: 50px;
  }

  .slide {
    height: 40vh;
    max-height: 300px;

    @media (min-width: 768px) {
      max-height: 450px;
    }

    .slide__image {
      height: 100%;
      object-fit: cover;
    }
  }
}
